:root {
  --font-main: "Inter";
  --light-body-color: #f8f8f8;
  --light-main-accent-color: #9bb537;
  --light-main-black-text-color: #050505;
  --light-description-text: rgba(5, 5, 5, .8);
  --dark-body-color: #161616;
  --dark-main-accent-color: #9bb537;
  --dark-main-white-text-color: #fff;
  --dark-description-text: rgba(255, 255, 255, .8);
  --dark-header-secondary-color: rgba(180, 175, 175, .518);
  --raiting-stars-color: #eea10c;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
  --animation-slow: 450ms cubic-bezier(.4, 0, .2, 1);
}

* {
  box-sizing: border-box;
}

body {
  background-color: var(--light-body-color);
  font-family: var(--font-main);
  flex-direction: column;
  margin: 0;
  font-size: 16px;
  display: flex;
  overflow-x: hidden;
}

main {
  flex-grow: 1;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.margin-top .support {
  margin-top: 0;
}

h1, h2, h3, h4, p, ul {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  padding-left: 0;
  list-style: none;
}

img {
  width: 100%;
  border-radius: 8px;
  display: block;
}

a:link {
  text-decoration: none;
}

.hiden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 100px;
    padding-right: 100px;
  }
}

