@media (max-width: 767px) {
  .time-wrapper {
    flex-grow: 1;
  }
}

.main-area-wrap {
  width: 141px;
  flex-direction: column;
  display: flex;
}

@media (max-width: 767px) {
  .main-area-wrap {
    flex-grow: 1;
  }
}

.main-ingredients-wrap {
  width: 188px;
  flex-direction: column;
  display: flex;
}

.select-btn, .time-options li, .main-area-wrapper, .area-options li, .main-ingredients-wrapper, .ingredients-options li {
  cursor: pointer;
  align-items: center;
  display: flex;
}

.main-time-wrap {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.select-btn {
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  justify-content: space-between;
  align-items: center;
  padding: 14px 18px;
  display: flex;
  position: relative;
}

.open-selected-menu {
  transform: rotate(-180deg);
}

.time-content, .area-content, .ingredients-content {
  background-color: var(--dark-main-white-text-color);
  display: none;
  position: relative;
}

.time-wrapper.active .time-content, .main-area-wrap.active .area-content, .main-ingredients-wrap.active .ingredients-content {
  display: block;
}

.time-select-hover {
  transition: var(--animation);
}

.time-select-hover:hover {
  color: var(--light-main-black-text-color);
}

.area-select-hover {
  transition: var(--animation);
}

.area-select-hover:hover {
  color: var(--light-main-black-text-color);
}

.time-options {
  width: 100%;
  max-height: 128px;
  color: rgba(5, 5, 5, .5);
  font-family: var(--font-main);
  z-index: 100;
  background-color: var(--dark-main-white-text-color);
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 14px;
  flex-direction: column;
  gap: 8px;
  padding: 14px 18px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow-y: auto;
  box-shadow: 0 4px 36px rgba(0, 0, 0, .02);
}

.area-options, .ingredients-options {
  width: 100%;
  max-height: 188px;
  color: rgba(5, 5, 5, .5);
  font-family: var(--font-main);
  z-index: 100;
  background-color: var(--dark-main-white-text-color);
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 14px;
  flex-direction: column;
  gap: 8px;
  padding: 14px 18px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow-y: auto;
  box-shadow: 0 4px 36px rgba(0, 0, 0, .02);
}

.tui-pagination .tui-ico-ellip {
  vertical-align: middle;
  background: url("data:image/png;base64,") no-repeat;
  font-size: 0;
  display: inline-block;
}

